___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --win-text: #106d10;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    position: relative;
    margin: 40px auto;
    width: 80vw;
    outline: 1px solid black;
    font-family: Roboto;
    font-size: 18px;
    min-width: 300px;
    max-width: 800px;
    overflow: hidden;
}

.app__header,
.app__footer {
    display: flex;
    justify-content: space-between;
    padding: 5px;
}

.app__game-field {
    display: grid;
    background: black;
}

.app__game-field_5x5 {
    grid-template-columns: repeat(5, auto);
    grid-template-rows: repeat(5, auto);
}

.app__game-field_10x10 {
    grid-template-columns: repeat(10, auto);
    grid-template-rows: repeat(10, auto);
}

.app__game-field_15x15 {
    grid-template-columns: repeat(15, auto);
    grid-template-rows: repeat(15, auto);
}

.cell {
    width: 30px;
    height: 30px;
    border: 1px solid #d4d4d4;
    background: #ffffff;
}

.button {
    cursor: pointer;
    background: #ffffff;
    height: 60px;
    width: 100px;
    border-radius: 6px;
    font-family: Roboto;
    transition: all 0.5s;
    font-size: 18px;
}

.button:disabled {
    cursor: default;
}

#vertical-clues,
#horizontal-clues {
    display: flex;
    background: #000000;
}

#vertical-clues {
    justify-content: flex-end;
    background: #ffffff;
}

#vertical-clues-wrapper {
    display: flex;
    border-bottom: 3px solid #000000;
    background: #000000;
}

#horizontal-clues {
    flex-direction: column;
    border-right: 3px solid #000000;
}

#inner-wrapper {
    display: flex;
}

#game-field-wapper {
    width: min-content;
    margin: 0 auto;
    padding: 20px 0;
}

.clue {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    width: 30px;
}

.clues-row-vert {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    height: auto;
}

.clues-row-hor {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: auto;
}

.border-bottom {
    /* border-bottom: 3px solid #000000; */
    margin-bottom: 3px;
}

.border-right {
    margin-right: 3px;
    /* border-right: 3px solid #000000; */
}

.border-right-clue {
    margin-right: 3px;
}

#space-fill {
    background: #ffffff;
    width: 100%;
    border-left: 1px solid #ffffff;
}

.picked {
    background: #000000;
}

.crossed {
    position: relative;
}
.crossed::after {
    content: '';
    display: block;
    position: absolute;
    height: 1px;
    width: 100%;
    background: #000000;
    top: 50%;
    transform: rotate(45deg);
}
.crossed::before {
    content: '';
    display: block;
    position: absolute;
    height: 1px;
    width: 100%;
    background: #000000;
    top: 50%;
    transform: rotate(-45deg);
}

#continue-game-btn {
    font-size: 17px;
}

.info-box {
    text-align: center;
    font-weight: 700;
}

#time-info {
    text-align: center;
    margin: 10px 0 0 0;
}

#win-modal {
    display: block;
    height: max-content;
    width: 100%;
    padding: 25px 0 0 0;
    text-align: center;
    color: var(--win-text);
}

#random-game-btn {
    font-size: 16px;
    height: 40px;
    width: 160px;
    margin-top: 5px;
}

.score-table {
    display: flex;
    position: absolute;
    width: 100%;
    background-color: #ffffff;
    min-height: 90%;
    top: 70px;
    padding: 20px;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.score-table__btn {
    width: 170px;
    height: 40px;
}

.score-table__item-wrapper {
    padding: 0 15px;
}

.score-table__item {
    margin-top: 15px;
}

@media (hover: hover) {
    .button:hover {
        background: rgb(194, 194, 194);
    }

    .button:disabled:hover {
        background-color: #ffffff;
    }
}
